@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&display=swap);
.movieRow{
    margin-bottom: 30px;
}

.movieRow h2{
    margin: 0px 0px 0px 30px;
}

.movieRow-listarea{
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow-list{
    width: 9999999px;
}
.movieRow-item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow-item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.2s;
}

.movieRow-item img:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
}
*{
  box-sizing: border-box;
}
body{
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists{
  margin-top: -150px;
}
.featured{
    height: 100vh;
}

.featured-vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured-horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured-name{
    font-size: 60px;
    font-weight: bold;
}

.featured-name{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured-points, .featured-year, .featured-seasons{
    display: inline-block;
    margin-right: 15px;
}
.featured-points{
    color: #46d369;
}

.featured-description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.watch-button, .mylist-button {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 10px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
    margin-top: 15px;
}

.watch-button:hover, .mylist-button:hover {
    opacity: 0.7;
}
.watch-button{
    background-color: #FFF;
    color: #000;
}

.mylist-button{
    background-color: #333;
    color: #fff;    
}

.featured-genres{
    margin-top: 15px;
    color: #999;
    font-size: 18px;
}
