.featured{
    height: 100vh;
}

.featured-vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured-horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured-name{
    font-size: 60px;
    font-weight: bold;
}

.featured-name{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured-points, .featured-year, .featured-seasons{
    display: inline-block;
    margin-right: 15px;
}
.featured-points{
    color: #46d369;
}

.featured-description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.watch-button, .mylist-button {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 10px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
    margin-top: 15px;
}

.watch-button:hover, .mylist-button:hover {
    opacity: 0.7;
}
.watch-button{
    background-color: #FFF;
    color: #000;
}

.mylist-button{
    background-color: #333;
    color: #fff;    
}

.featured-genres{
    margin-top: 15px;
    color: #999;
    font-size: 18px;
}